import React from "react";
import { Query, Mutation } from "react-apollo";
import { Link } from "react-router-dom";
import CKEditor from "react-ckeditor-component";
import {
  GET_USER_RECIPES,
  UPDATE_USER_RECIPE,
  DELETE_USER_RECIPE,
  GET_ALL_RECIPES,
  GET_CURRENT_USER
} from "../../queries";
import Spinner from "../Spinner";

class UserRecipes extends React.Component {
  state = {
    _id: "",
    name: "",
    imageUrl: "",
    category: "Breakfast",
    description: "",
    instructions: "",
    modal: false
  };

  handleChange = event => {
    const { name, value } = event.target;
    this.setState({
      [name]: value
    });
  };

  handleEditChange = event => {
    const newContent = event.editor.getData();
    this.setState({ instructions: newContent });
  };

  handleDelete = deleteUserRecipe => {
    const confirmDelete = window.confirm(
      "Are you sure you want to delete this recipe?"
    );
    if (confirmDelete) {
      deleteUserRecipe().then(({ data }) => {
        //
      });
    }
  };

  handleSubmit = (event, updateUserRecipe) => {
    event.preventDefault();
    updateUserRecipe().then(({ data }) => {
      console.log(data);
      this.closeModal();
    });
  };

  loadRecipe = recipe => {
    this.setState({ ...recipe, modal: true });
  };

  closeModal = () => {
    this.setState({ modal: false });
  };

  render() {
    const { username } = this.props;
    const { modal } = this.state;
    return (
      <Query query={GET_USER_RECIPES} variables={{ username }}>
        {({ data, loading, error }) => {
          if (loading) return <Spinner />;
          if (error) return <div className="App">Error</div>;
          return (
            <ul>
              {modal && (
                <EditRecipeModal
                  recipe={this.state}
                  closeModal={this.closeModal}
                  handleChange={this.handleChange}
                  handleEditChange={this.handleEditChange}
                  handleSubmit={this.handleSubmit}
                />
              )}
              <h3>Your Recipes</h3>
              {!data.getUserRecipes.length && (
                <p>
                  <strong>You have not added any recipes yet.</strong>
                </p>
              )}
              {data.getUserRecipes.map(recipe => (
                <li key={recipe._id} style={{ border: "2px solid black" }}>
                  <Link to={`/recipes/${recipe._id}`}>
                    <p>{recipe.name}</p>
                  </Link>
                  <p style={{ marginBottom: "0" }}>Likes: {recipe.likes}</p>
                  <Mutation
                    mutation={DELETE_USER_RECIPE}
                    variables={{ _id: recipe._id }}
                    refetchQueries={() => [
                      { query: GET_ALL_RECIPES },
                      { query: GET_CURRENT_USER }
                    ]}
                    update={(cache, { data: { deleteUserRecipe } }) => {
                      const { getUserRecipes } = cache.readQuery({
                        query: GET_USER_RECIPES,
                        variables: { username }
                      });
                      cache.writeQuery({
                        query: GET_USER_RECIPES,
                        variables: { username },
                        data: {
                          getUserRecipes: getUserRecipes.filter(
                            recipe => recipe._id !== deleteUserRecipe._id
                          )
                        }
                      });
                    }}
                  >
                    {(deleteUserRecipe, attrs = {}) => (
                      <React.Fragment>
                        <button
                          className="button-primary"
                          onClick={() => this.loadRecipe(recipe)}
                        >
                          Update
                        </button>
                        <button
                          className="delete-button"
                          onClick={() => this.handleDelete(deleteUserRecipe)}
                        >
                          {attrs.loading ? "Deleting..." : "Delete"}
                        </button>
                      </React.Fragment>
                    )}
                  </Mutation>
                </li>
              ))}
            </ul>
          );
        }}
      </Query>
    );
  }
}

const EditRecipeModal = ({
  handleSubmit,
  recipe,
  handleChange,
  handleEditChange,
  closeModal
}) => (
  <Mutation
    mutation={UPDATE_USER_RECIPE}
    variables={{
      _id: recipe._id,
      name: recipe.name,
      imageUrl: recipe.imageUrl,
      category: recipe.category,
      description: recipe.description,
      instructions: recipe.instructions
    }}
  >
    {updateUserRecipe => (
      <div className="modal modal-open">
        <div className="modal-inner">
          <div className="modal-content">
            <form
              onSubmit={event => handleSubmit(event, updateUserRecipe)}
              className="modal-content-inner"
            >
              <h4>Edit Recipe</h4>
              <input
                type="text"
                name="name"
                placeholder="Recipe Name"
                onChange={handleChange}
                value={recipe.name}
              />
              <input
                type="text"
                name="imageUrl"
                placeholder="Recipe Image"
                onChange={handleChange}
                value={recipe.imageUrl}
              />
              <select
                name="category"
                onChange={handleChange}
                value={recipe.category}
              >
                <option value="Breakfast">Breakfast</option>
                <option value="Lunch">Lunch</option>
                <option value="Dinner">Dinner</option>
                <option value="Snack">Snack</option>
              </select>
              <input
                type="text"
                name="description"
                placeholder="Add description"
                onChange={handleChange}
                value={recipe.description}
              />
              <CKEditor
                name="instructions"
                content={recipe.instructions}
                events={{ change: handleEditChange }}
              />
              <hr />
              <div className="modal-buttons">
                <button type="submit" className="button-primary">
                  Update
                </button>
                <button onClick={closeModal}>Cencel</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    )}
  </Mutation>
);

export default UserRecipes;
